<style lang="less" scoped>
.header-action {
  label {
    color: #009688;
    font-size: 17px;
    cursor: pointer;
    font-weight: bold;
  }

  span {
    color: rgb(122, 118, 118);
  }
}
</style>

<template>
  <div>
    <custom-table apiUrl="/ds/getObjectPermission2Oneself" :columns="permissionColumns" :apiParams="permissionParams" v-model="reload" @on-selection-change="onSelectionChange" @on-refesh-change='onRefeshChange'>
      <!-- <div slot="header" class="permission-container-btn">
        <Button icon="md-add" type="primary" @click="addPermission">添加权限</Button>
        <Button icon="md-remove" type="primary" @click="deletePermission" :disabled="deleteBtnDisable">移除权限</Button>
      </div> -->

      <div slot="header" class="header-action">
        <label @click="addPermission">添加权限</label>
        <span>-添加权限</span>

        <label @click="deletePermission">移除权限</label>
        <span>-移除权限</span>
      </div>
    </custom-table>

    <member-modal v-model="isShowModal" width="600" footerBtnAlign="right" title="权限列表" @on-ok="savePermission">
      <div style="max-height:350px;overflow:auto">
        <Tree :data="allPermissionData" :multiple="true" @on-select-change="onCheckChange" :load-data="loadData"></Tree>
      </div>
    </member-modal>
  </div>
</template>

<script>
import MemberModal from "@/components/modal/Modal";
import {
  getAllPermissionData,
  addRolePermission,
  deleteRolePermission
} from "@/services/addressBookService.js";
import CustomTable from "@/views/addressBook/organization/instance/CustomTable";
export default {
  name: "job-permission",

  components: {
    CustomTable,
    MemberModal
  },

  props: {
    jobId: {
      type: String
    }
  },

  data() {
    return {
      permissionParams: {
        objectName: "role",
        objectId: this.jobId,
        page: 1,
        limit: 10,
        start: 0
      },

      permissionColumns: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          title: "名称",
          key: "name"
        },
        {
          title: "操作",
          key: "action",
          width: 120,
          align: "center",
          render: (h, params) => {
            return h(
              "Button",
              {
                props: {
                  type: "error",
                  size: "small"
                },
                style: {
                  cursor: "pointer"
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: "确认",
                      content: "确认删除该权限？",
                      onOk: () => {
                        deleteRolePermission(this.jobId, params.row.id)
                          .then(res => {
                            if (res.success) {
                              this.$Message.success("删除成功!");
                              this.reload = true;
                              this.$emit("on-permission-change", true);
                            }
                          })
                          .catch(error => {
                            this.$Message.error(error.data.message);
                          });
                      }
                    });
                  }
                }
              },
              "删除"
            );
          }
        }
      ],

      //模态框参数
      isShowModal: false,
      allPermissionData: [],
      selectPermissionNode: [],
      selectDeletePermission: [],
      reload: false
    };
  },

  methods: {
    listUserChangePage(currentPage) {
      this.getListUsers(currentPage, this.pageSize);
    },

    addPermission() {
      this.isShowModal = true;
      this.getAllPermissionData();
    },

    onRefeshChange(val) {
      if (!val) {
        this.reload = val;
      }
    },

    deletePermission() {
      let multiId = [];
      this.selectDeletePermission.forEach(val => {
        multiId.push(val.id);
      });
      if (multiId) {
        deleteRolePermission(this.jobId, multiId.join(","))
          .then(res => {
            if (res.success) {
              this.$Message.success("删除成功!");
              this.selectPermissionNode = [];
              this.reload = true;
              this.$emit("on-permission-change", true);
            }
          })
          .catch(error => {
            this.$Message.error(error.data.message);
          });
      }
    },

    onSelectionChange(selection) {
      if (selection.length > 0) {
        this.selectDeletePermission = selection;
      }
    },

    //点击节点选择权限
    onCheckChange(node) {
      this.selectPermissionNode = node;
    },

    //保存添加的权限
    savePermission() {
      let multiId = [];
      this.selectPermissionNode.forEach(val => {
        multiId.push(val.id);
      });
      if (multiId) {
        addRolePermission(this.jobId, multiId.join(",")).then(res => {
          if (res.success) {
            this.$Message.success(res.message);
            this.reload = true;
            this.$emit("on-permission-change", true);
            this.isShowModal = false;
          }
        });
      }
    },

    //加载所有权限数据
    getAllPermissionData() {
      if (this.allPermissionData.length === 0) {
        getAllPermissionData(0).then(res => {
          res.tableContent.forEach(val => {
            this.allPermissionData.push({
              title: val.name,
              id: val.id,
              loading: false,
              children: []
            });
          });
        });
      }
    },

    //异步加载权限数据
    loadData(item, callback) {
      getAllPermissionData(item.id).then(res => {
        let data = [];
        res.tableContent.forEach(val => {
          if (val.leaf === "false") {
            data.push({
              title: val.name,
              id: val.id,
              loading: false,
              children: []
            });
          } else {
            data.push({
              title: val.name,
              id: val.id
            });
          }
        });
        callback(data);
      });
    }
  }
};
</script>




